import React, { Component } from 'react';
import { Link } from "react-router-dom";

import AuthService from '../../../services/authenticity/auth-service.js';

/** Estilos CSS*/
import '../../../styles/styleGeneral.css';
import '../../../styles/styleCard.css';
/**Estilos importados*/
import Card from 'react-bootstrap/Card';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import Nav from 'react-bootstrap/Nav';

class Inicio extends Component {
          constructor(props){
            super(props);
            this.state = {
                currentUser: undefined,
                showStudent: false,
                showTeacher: false,
                showAdmin: false,
                url: ""
            };
        }
          
          
            componentDidMount() {
              //Take info about server
              const user = AuthService.getCurrentUser();
          
              if (user) {
                this.setState({
                  currentUser: user,
                  showStudent: user["rol"].includes("S"),
                  showTeacher: user["rol"].includes("T"),
                  showAdmin: user["rol"].includes("A"),
                });
              }
          
              if (this.state.showStudents) {
                this.setState({
                  url: '/student/groups'
                });
              }
              else if (this.state.showAdmin) {
                this.setState({
                  url: "/teacher/groups"
                });
              }
              else if (this.state.showTeacher) {
                this.setState({
                  url: "/admin"
                });
              }
              else {
                this.setState({
                  url: window.location.pathname
                });
              }
            }
            

    /*Dibuja la pagina  */
    render() {
        const { currentUser, showStudent, showTeacher, showAdmin } = this.state;

        return (
           <div className="container">
               <Card className={"card-long"}>
                   <Card.Body>
                   <div className={"section-title"}>
                        <h2>Qu es Creativa</h2>
                    </div>
                    <div className='row-edit'>
                        <div className={"section-title"}>
                            <h5>De  qu trata</h5>
                        </div>
                        <p>Es una aplicacin para fomentar la escritura creativa de una manera didctica a travs de desafos de distintas temticas que propondr el profesor.</p>
                        <div className={"section-title"}>
                            <h5>Uso de la aplicacin</h5>
                        </div>
                        <p>En el panel superior te encontrars con diferentes apartados de la aplicacin web, que te mostrarn una funcionalidad diferente cada uno.</p>
                        <div className={"section-title"}>
                            <h5>Inicio</h5>
                        </div>
                        <p>Es la pgina en la que te encuentras, en ella se ofrece un breve resumen de la aplicacin y un pequeo tutorial sobre cmo moverte por la plataforma.</p>
                        <div className={"section-title"}>
                            <h5>Tablero</h5>
                        </div>
                        {showStudent ? ( <>
                            <Link to="/student/groups">Grupos</Link>
                            <p>Desde esta pestaa realizaremos todo lo relacionado con la escritura creativa. 
                            Cuando clicamos en ella vamos a la pestaa principal "Grupos". En "Grupos" tenemos acceso a casi toda la aplicacin de manera
                            rpida y sencilla. Podemos pedir entrar a un grupo, ver nuestros grupos, crear nuestro equipo, pedir que nos inviten a uno, continuar un escrito, etc. </p> </>) : ("")}
                         {showTeacher ? ( <>
                            <Link to="/teacher/groups">Grupos</Link>
                            <p>Desde esta pestaa realizaremos el control de los estudiantes creando desafos, viendo escritos de los estudiantes, los equipos que ellos han formado,
                                los propios estudiantes, tambin se ofrece la posibilidad de renombrar el grupo...
                            </p> </>) : ("")}
                            {showStudent ? ( <>
                            <Link to="/student/challengesTabs">Crear Escrito</Link>
                            <p> Debajo de esta pestaa encontramos las subsidiarias, primero Crear Escrito para aquellos que no estn empezados an, dividido en individual o 
                            colaborativo, si el escrito fuese en equipo. </p> </>) : ("")}
                            {showTeacher ? ( <>
                            <Link to="/teacher/students">Estudiantes</Link>
                            <p> En esta pestaa podemos ver la lista de los estudiantes con los que cuenta el profesor. </p> </>) : ("")}
                            {showStudent ? ( <>
                            <Link to="/student/writingsTabs">Escrito</Link>
                            <p>  Debajo de este est Escrito, para editar los escritos ya empezados. Adems, cuenta con versiones, por lo que si no te gusta el rumbo que est tomando
                            tu escrito puedes volver a una versin anterior, y volver a usarla como que fuese la actual. </p>  </>) : ("")}  
                            {showTeacher ? ( <>
                            <Link to="/teacher/applicants">Solicitantes</Link>
                            <p> Aqu se pueden ver los alumnos que quieren unirse bien sea a la plataforma o a algn grupo que imparte el profesor. </p> </>) : ("")}
                            {showStudent ? ( <>
                            <Link to="/student/teams">Equipos</Link>
                            <p> En Equipos encontramos los equipos que lideramos y de los que formamos parte.  </p> </>) : ("")}
                              {showTeacher ? ( <>
                            <Link to="/teacher/createGroup">Crear Grupo</Link>
                            <p> Aqu el profesor puede crear un nuevo grupo de la temtica que desee. </p> </>) : ("")}
                            {showStudent && (
                            <Link to="/student/visualizecollections">Colecciones</Link>
                            )}
                            {showTeacher && (
                            <Link to="/teacher/collections">Colecciones</Link>
                            )}
                            <p>  Y, por ltimo, en colecciones encontramos un lbum de desafos creados por el profesor segn el grupo, donde se pueden ver los escritos 
                            que responden a esos desafos. </p>
                            
                       
                        <div className={"section-title"}>
                            <h5>
                            <Link to="/profile">Perfil</Link>
                           </h5> 
                        </div>
                        <p>Desde esta pestaa podemos ver nuestro perfil y tambin podemos configurarlo.</p>
                        
                             {showStudent ? (<><div className={"section-title"}>
                             <h5>   
                            <Link to="/student/messenger">Mensajera</Link>
                            </h5>
                        </div> 
                        <p>Se pueden enviar mensajes entre un estudiante que forme parte de un equipo y otro que no de dos formas: el estudiante que est en un equipo puede pedir a otro estudiante
                             que entre en su equipo y otra forma, que un estudiante que no est en un equipo, enve una peticin para entrar.
                        </p> </>) : ("")}
                      
                    </div>
                   </Card.Body>
               </Card>
           </div>
        );


    }


}

export default Inicio;